@tailwind base;
@tailwind components;
@tailwind utilities;
@font-face {
    font-family: 'Gilroy';
    src: local('Gilroy-Regular'),
        url('./assets/font/Gilroy-Regular.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
  }
  @font-face {
    font-family: 'Circular';
    src: local('Circular'),
        url('./assets/font/Circular.ttf') format('truetype');
    font-style: normal;
  }

  html, body{
  padding: 0 !important;
  margin: 0 !important;
  box-sizing: border-box !important;
  width: 100vw;
  }


@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@300&family=Mulish:wght@400;600&family=Rubik:wght@400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');